import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { HttpClientModule } from '@angular/common/http';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { LoginPage } from '../pages/login/login';
import { ServiceProvider } from '../providers/service/service';
import { RegisterPage } from '../pages/register/register';
import { TabsPage } from '../pages/tabs/tabs';
import { AccountPage } from '../pages/account/account';
import { DocsPage } from '../pages/docs/docs';
import { AnnouncePage } from '../pages/announce/announce';
import { UserPage } from '../pages/user/user';
import { DocsuserPage } from '../pages/docsuser/docsuser';
import { DocslistPage } from '../pages/docslist/docslist';
import { FolderdetailPage } from '../pages/folderdetail/folderdetail';
import { StatusPage } from '../pages/status/status';
import { WorkdetailPage } from '../pages/workdetail/workdetail';


@NgModule({
  declarations: [
    MyApp,
    TabsPage,
    HomePage,
    LoginPage,
    RegisterPage,
    AccountPage,
    DocsPage,
    AnnouncePage,
    UserPage,
    DocsuserPage,
    DocslistPage,
    FolderdetailPage,
    StatusPage,
    WorkdetailPage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp, { mode : "ios"})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    TabsPage,
    HomePage,
    LoginPage,
    RegisterPage,
    AccountPage,
    DocsPage,
    AnnouncePage,
    UserPage,
    DocsuserPage,
    DocslistPage,
    FolderdetailPage,
    StatusPage,
    WorkdetailPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    ServiceProvider
  ]
})
export class AppModule {}



