import { Component } from "@angular/core";
import { FormBuilder } from "@angular/forms";
import {
  AlertController,
  App,
  LoadingController,
  NavController,
  NavParams,
  ToastController,
} from "ionic-angular";
import { ServiceProvider } from "../../providers/service/service";

/**
 * Generated class for the FolderdetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-folderdetail",
  templateUrl: "folderdetail.html",
})
export class FolderdetailPage {
  public uid: any;
  public role: any;
  public loading: any;
  public files: any;
  public folder: any;

  constructor(
    public app: App,
    public navCtrl: NavController,
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public toastCtrl: ToastController,
    public db: ServiceProvider,
    public formBuilder: FormBuilder
  ) {
    this.uid = localStorage.getItem("uidmitwork");
    this.role = localStorage.getItem("role");

    this.files = navParams.get("data");
    // console.log(this.files);

    this.db
      .getServiceJson({
        type: "storagerole",
        diractory_id: this.files.diractory_id,
        role: this.role,
      })
      .then(async (data: any) => {
        if (data != null) {
          if (data.status == 200) {
            // console.log(data.result);
            this.folder = await data.result;
          }
        }
      });
  }

  ionViewDidLoad() {
    console.log("ionViewDidLoad FolderdetailPage");
  }
}



