import { Component } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import {
  AlertController,
  App,
  LoadingController,
  NavController,
  NavParams,
  ToastController,
} from "ionic-angular";
import { ServiceProvider } from "../../providers/service/service";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { environment } from "../../environment";

/**
 * Generated class for the WorkdetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-workdetail",
  templateUrl: "workdetail.html",
})
export class WorkdetailPage {

  fileCategories: FileCategory[] = [{ label: "", selectedFile: null }];
  uploadStatus: string | null = null;

  public uid: any;
  public role: any;
  public loading: any;

  public Forms: FormGroup;
  public FormsError: any;

  public worktype: Number;
  public acc: any;
  public doclists: any;
  public doclistwork: any;
  public doclisthighwork: any;
  public doclistworkpoint: any;

  data: any;
  content: any;
  menu: string; // Set a default value if needed

  displayMenu1: boolean = false;
  displayMenu2: boolean = false;
  displayMenu3: boolean = false;
  displayMenu4: boolean = false;
  displayMenu5: boolean = false;
  displayMenu6: boolean = false;
  displayMenu7: boolean = false;
  displayMenu8: boolean = false;


  // Define the checkbox states
  reportActivity: boolean;
  waitingForReview: boolean;
  waitingForApproval: boolean;
  approved: boolean;
  noapproved: boolean;
  remarks: string;

  constructor(
    public app: App,
    public navCtrl: NavController,
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public toastCtrl: ToastController,
    public db: ServiceProvider,
    public formBuilder: FormBuilder,
    private http: HttpClient
  ) {
    this.uid = localStorage.getItem("uidmitwork");
    this.role = localStorage.getItem("role");

    this.data = navParams.get("data");
    this.menu = "docs";

    // console.log(this.data);

    switch (this.menu) {
      case "docs":
        this.doclistwork = this.data;
        // console.log(this.doclistwork);

        this.db.getServiceJson({
          "type": "alldocslistbycodegen",
          "codegen": this.doclistwork.codegen
        }).then(async (data: any) => {
          // console.log(data);
          if (data != null) {
            if (data.status == 200) {
              // console.log(data.result);
              this.reportActivity = data.result.status_work1 == 'Approve' ? true : false;
              this.waitingForReview = data.result.status_work2 == 'Approve' ? true : false;
              this.waitingForApproval = data.result.status_work3 == 'Approve' ? true : false;
              this.approved = data.result.status_work4 == 'Approve' ? true : false;
              this.noapproved = data.result.status_work5 == 'Approve' ? true : false;
              this.remarks = data.result.remarks;
              // this.notifications("อัพเดทสถานะสำเร็จ");
            } else {
              this.notifications("อัพเดทสถานะไม่สำเร็จ");
            }
          }
        });

        break;

      case "point":
        this.doclistwork = this.data;
        // console.log(this.doclistwork);
        break;

      case "card":
        this.doclistwork = this.data;
        // console.log(this.doclistwork);
        break;
    }

  }

  async onSegmentSelect(selectedMenu: any) {
    this.menu = await selectedMenu.value;
    this.autoRefresh();
  }


  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        this.uid = localStorage.getItem("uidmitwork");
        this.role = localStorage.getItem("role");

        switch (this.menu) {
          case "docs":
            this.doclistwork = this.data;
            this.db.getServiceJson({
              "type": "alldocslistbycodegen",
              "codegen": this.doclistwork.codegen
            }).then(async (data: any) => {
              // console.log(data);
              if (data != null) {
                if (data.status == 200) {
                  // console.log(data.result);
                  this.reportActivity = data.result.status_work1 == 'Approve' ? true : false;
                  this.waitingForReview = data.result.status_work2 == 'Approve' ? true : false;
                  this.waitingForApproval = data.result.status_work3 == 'Approve' ? true : false;
                  this.approved = data.result.status_work4 == 'Approve' ? true : false;
                  this.noapproved = data.result.status_work5 == 'Approve' ? true : false;
                  this.remarks = data.result.remarks;
                  // this.notifications("อัพเดทสถานะสำเร็จ");
                } else {
                  this.notifications("อัพเดทสถานะไม่สำเร็จ");
                }
              }
            });
            break;

          case "point":
            this.doclistwork = this.data;
            // console.log(this.doclistwork);
            break;

          case "card":
            this.doclistwork = this.data;
            // console.log(this.doclistwork);
            break;
        }
        await this.loading.dismiss();
      });
      refresher.complete();
    }, 1000);
  }

  async autoRefresh() {
    const refresher = {
      complete: () => {
        // console.log("Refresher complete");
      },
    };
    await this.doRefresh(refresher);
  }

  async ionViewWillEnter() {
    // console.log("ionViewWillEnter");
    await this.autoRefresh();
  }

  async ionViewDidLoad() {
    // console.log("ionViewDidLoad WorkDetailPage");
  }

  // Log status function
  async logStatus() {

    await this.db.getServiceJson({
      "type": "updatestatuswork",
      "codegen": this.doclistwork.codegen,
      "status_work1": this.reportActivity == true ? 'Approve' : 'Pending',
      "status_work2": this.waitingForReview == true ? 'Approve' : 'Pending',
      "status_work3": this.waitingForApproval == true ? 'Approve' : 'Pending',
      "status_work4": this.approved == true ? 'Approve' : 'Pending',
      "status_work5": this.noapproved == true ? 'Approve' : 'Pending',
      "remarks": this.remarks
    }).then(async (data: any) => {
      // console.log(data);
      if (data != null) {
        if (data.status == 200) {
          this.notifications("อัพเดทสถานะสำเร็จ");
          await this.autoRefresh();
        } else {
          this.notifications("อัพเดทสถานะไม่สำเร็จ");
        }
      }
    });

  }

  // menu 3
  async lookfile(file) {
    const url = environment.URL_Work_download + file;
    await window.open(url, "_blank");
  }

  async deletefile(menu: string, file_id) {
    const confirm = await this.alertCtrl.create({
      message: "ต้องการลบไฟล์หรือไม่?",
      cssClass: "alertCustomCssdelete",
      enableBackdropDismiss: false,
      buttons: [
        {
          text: "ลบ",
          handler: () => {
            this.db.getServiceJson({
              "type": "deletefilework",
              "file_id": file_id
            }).then((data: any) => {
              // console.log(data);
              if (data.status == 200) {
                this.notifications("ลบเอกสารสำเร็จ");
                if (menu == 'point') {
                  this.menu = 'point';
                } else if (menu == 'card') {
                  this.menu = 'card';
                }
                this.navCtrl.pop()
              } else {
                this.notifications("ลบเอกสารไม่สำเร็จ");
              }
            });
          }
        },
        {
          text: "ยกเลิก",
          handler: async () => { },
        },
      ],
    });
    await confirm.present();

  }


  // ฟังก์ชันเมื่อมีการเลือกไฟล์
  async onFileSelected(event: any, index: number) {
    const file = await event.target.files[0];
    if (file && file.type === "application/pdf") {
      this.fileCategories[index].selectedFile = file;
    } else {
      this.notifications("กรุณาเลือกไฟล์ PDF เท่านั้น");
      this.fileCategories[index].selectedFile = null;
    }
  }


  // ฟังก์ชันอัปโหลดไฟล์
  async uploadFiles(menu: string, work_type_db: string, work_id: any, column: string) {

    const confirm = await this.alertCtrl.create({
      subTitle: "อัพโหลด",
      message: "อัพโหลดใช่หรือไม่?",
      cssClass: "alertCustomCssdetail",
      enableBackdropDismiss: false,
      buttons: [
        {
          text: "ตกลง",
          handler: () => {
            const formData = new FormData();

            // วนลูปเพิ่มไฟล์ที่ถูกเลือกลงใน FormData
            this.fileCategories.forEach((category, index) => {
              if (category.selectedFile) {
                formData.append(
                  `files[${index}]`,
                  category.selectedFile,
                  category.selectedFile.name
                );
              }
            });

            if (work_type_db == 'updatefile_work') {

              if (menu == 'point') {
                if (this.doclistwork.file_7_name) {
                  this.notifications('กรุณาลบไฟล์ก่อนหน้าเพื่อทำการอัพโหลดใหม่');
                  return;
                } else {
                  formData.append("user_id", this.uid.toString());
                  formData.append("work_type_db", work_type_db);
                  formData.append("work_type_id", this.doclistwork.work_type_id);
                  formData.append("work_id", work_id);
                  formData.append("column", column);
                }
              } else if (menu == 'card') {
                if (this.doclistwork.file_8_name) {
                  this.notifications('กรุณาลบไฟล์ก่อนหน้าเพื่อทำการอัพโหลดใหม่');
                  return;
                } else {
                  formData.append("user_id", this.uid.toString());
                  formData.append("work_type_db", work_type_db);
                  formData.append("work_type_id", this.doclistwork.work_type_id);
                  formData.append("work_id", work_id);
                  formData.append("column", column);
                }
              }

            }

            if (!formData.has("files[0]")) {
              // ตรวจสอบว่ามีไฟล์ใน FormData หรือไม่
              this.notifications("กรุณาเลือกไฟล์อย่างน้อยหนึ่งไฟล์ก่อนอัปโหลด");
              return;
            }

            const headers = new HttpHeaders();
            headers.append("Accept", "application/json");

            // ส่งคำร้อง HTTP POST ไปยัง API สำหรับอัปโหลด
            this.http.post(environment.URL_Work_upload, formData, { headers, }).subscribe((response: any) => {
              console.log(response[0]);
              if (response[0].status === "error") {
                this.notifications("มีไฟล์ชื่อนี้อยู่แล้ว");
                this.autoRefresh();

              } else {
                this.notifications("อัปโหลดไฟล์เรียบร้อย");

                if (work_type_db == 'updatefile_work') {
                  if (menu == 'point') {
                    this.menu = 'point';
                  } else if (menu == 'card') {
                    this.menu = 'card';
                  }
                }

                this.fileCategories.forEach((category) => {
                  category.selectedFile = null;
                });
                this.navCtrl.pop();
              }
            },
              (error) => {
                this.notifications("เกิดข้อผิดพลาดในการอัปโหลดไฟล์");
              }
            );
          },
        },
        {
          text: "ยกเลิก",
          handler: async () => { },
        },
      ],
    });
    await confirm.present();

  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: "bottom",
    });
    await toast.present();
  }
}

interface FileCategory {
  label: string;
  selectedFile: File | null;
}



