import { Component } from "@angular/core";
import {
  AlertController,
  App,
  LoadingController,
  NavController,
  NavParams,
  ToastController,
} from "ionic-angular";
import { ServiceProvider } from "../../providers/service/service";

/**
 * Generated class for the StatusPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-status",
  templateUrl: "status.html",
})
export class StatusPage {

  public uid: any;
  public role: any;
  public loading: any;
  public email: any;
  public password: any;
  public name: any;
  public statuslist: any;


  reportActivity: boolean;
  waitingForReview: boolean;
  waitingForApproval: boolean;
  approved: boolean;
  noapproved: boolean;
  remarks: string;

  constructor(
    public app: App,
    public navCtrl: NavController,
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public toastCtrl: ToastController,
    public db: ServiceProvider
  ) {
    this.uid = localStorage.getItem("uidmitwork");
    this.role = localStorage.getItem("role");

    this.statuslist = navParams.get("data");
    // console.log(this.statuslist);


    this.db.getServiceJson({ "type": "account", "user_id": this.uid }).then(async (data: any) => {
      // console.log(data);
      if (data != null) {
        if (data.status == 200) {
          this.email = await data.result.email;
          this.password = await data.result.password;
          this.name = await data.result.name;
        }
      }
    });


    this.db.getServiceJson({
      "type": "alldocslistbycodegen",
      "codegen": this.statuslist.codegen
    }).then(async (data: any) => {
      // console.log(data);
      if (data != null) {
        if (data.status == 200) {
          // console.log(data.result);

          if (this.statuslist.work_name == 'การอบรม') {
            this.approved = data.result.status_work4 == 'Approve' ? true : false;
            this.noapproved = data.result.status_work5 == 'Approve' ? true : false;
            this.remarks = data.result.remarks;
          } else {
            this.reportActivity = data.result.status_work1 == 'Approve' ? true : false;
            this.waitingForReview = data.result.status_work2 == 'Approve' ? true : false;
            this.waitingForApproval = data.result.status_work3 == 'Approve' ? true : false;
            this.approved = data.result.status_work4 == 'Approve' ? true : false;
            this.noapproved = data.result.status_work5 == 'Approve' ? true : false;
            this.remarks = data.result.remarks;
          }
          // this.notifications("อัพเดทสถานะสำเร็จ");
        } else {
          this.notifications("อัพเดทสถานะไม่สำเร็จ");
        }
      }
    });


  }

  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        this.uid = localStorage.getItem("uidmitwork");
        await this.db.getServiceJson({ "type": "account", "user_id": this.uid }).then(async (data: any) => {
          // console.log(data);
          if (data != null) {
            if (data.status == 200) {
              this.email = await data.result.email;
              this.password = await data.result.password;
              this.name = await data.result.name;
              await this.loading.dismiss();
            }
          }
        });
      });
      refresher.complete();
    }, 1000);
  }

  async autoRefresh() {
    const refresher = {
      complete: () => {
        // console.log("Refresher complete");
      },
    };
    await this.doRefresh(refresher);
  }

  async ionViewWillEnter() {
    // console.log("ionViewWillEnter");
    await this.autoRefresh();
  }

  async ionViewDidLoad() {
    // await console.log("ionViewDidLoad StatusPage");
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: "bottom",
    });
    await toast.present();
  }
}



