import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AlertController, App, NavController, ToastController } from 'ionic-angular';
import { ServiceProvider } from '../../providers/service/service';
import { RegisterPage } from '../register/register';
import { TabsPage } from '../tabs/tabs';


/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  public loginForm: FormGroup;
  public loginError: any;

  constructor(
    public app: App,
    public navCtrl: NavController,
    public db: ServiceProvider,
    public formBuilder: FormBuilder,
    public toastCtrl: ToastController,
    public alertCtrl: AlertController
  ) {
    this.loginForm = formBuilder.group({
      email: ['', Validators.compose([Validators.required, Validators.email])],
      password: ['', [
        Validators.required,
        Validators.minLength(6),
        Validators.maxLength(50)
      ]],
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }

  async login() {

    localStorage.setItem('uidmitwork', null);
    localStorage.setItem('role', null);
    let data = this.loginForm.value;

    if (!data.email) {
      return;
    }

    let credentials = {
      type: "login",
      email: data.email,
      password: data.password
    };

    await this.db.getServiceJson(credentials).then(async (data: any) => {
      if (data != null) {
        if (data.status === 200) {
          if (data.result === 401) {
            this.notifications("อีเมลหรือชื่อผู้ใช้ไม่ถูกต้อง");
            return;
          } else {
            await localStorage.setItem('uidmitwork', await data.result.user_id);
            await localStorage.setItem('role', await data.result.role);
            await this.navCtrl.setRoot(TabsPage);
          }
        } else {
          await this.navCtrl.setRoot(LoginPage);
          localStorage.removeItem('uidmitwork');
          localStorage.removeItem('role');
          localStorage.clear();
        }
      } else {
        await this.navCtrl.setRoot(LoginPage);
        localStorage.removeItem('uidmitwork');
        localStorage.removeItem('role');
        localStorage.clear();
      }
    });

  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: 'bottom'
    });
    await toast.present();
  }

  async btnregister() {
    await this.navCtrl.setRoot(RegisterPage);
  }

}



