import { Component } from "@angular/core";
import {
  AlertController,
  App,
  LoadingController,
  NavController,
  NavParams,
  ToastController,
} from "ionic-angular";
import { ServiceProvider } from "../../providers/service/service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";

/**
 * Generated class for the AnnouncePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-announce",
  templateUrl: "announce.html",
})
export class AnnouncePage {
  public uid: any;
  public role: any;
  public loading: any;
  public email: any;
  public username: any;
  public password: any;
  public name: any;

  public Forms: FormGroup;
  public FormsError: any;
  public acc: any;

  constructor(
    public app: App,
    public navCtrl: NavController,
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public toastCtrl: ToastController,
    public db: ServiceProvider,
    public formBuilder: FormBuilder
  ) {
    this.uid = localStorage.getItem("uidmitwork");
    this.role = localStorage.getItem("role");

    this.db
      .getServiceJson({ type: "account", user_id: this.uid })
      .then(async (data: any) => {
        // console.log(data);
        if (data != null) {
          if (data.status == 200) {
            this.email = await data.result.email;
            this.password = await data.result.password;
            this.name = await data.result.name;
          }
        }
      });

    this.db.getServiceJson({ type: "allannounce" }).then(async (data: any) => {
      // console.log(data);
      if (data != null) {
        if (data.status == 200) {
          this.acc = data.result.announce_content;
        }
      }
    });

    this.Forms = formBuilder.group({
      anndata: ["", Validators.compose([Validators.required])],
    });
  }

  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        this.uid = localStorage.getItem("uidmitwork");
        await this.db
          .getServiceJson({ type: "account", user_id: this.uid })
          .then(async (data: any) => {
            // console.log(data);
            if (data != null) {
              if (data.status == 200) {
                this.email = await data.result.email;
                this.password = await data.result.password;
                this.name = await data.result.name;
                await this.loading.dismiss();
              }
            }
          });

        await this.db
          .getServiceJson({ type: "allannounce" })
          .then(async (data: any) => {
            // console.log(data);
            if (data != null) {
              if (data.status == 200) {
                this.acc = data.result.announce_content;
              }
            }
          });
      });
      refresher.complete();
    }, 1000);
  }

  async autoRefresh() {
    const refresher = {
      complete: () => {
        console.log("Refresher complete");
      },
    };
    await this.doRefresh(refresher);
  }

  async ionViewWillEnter() {
    console.log("ionViewWillEnter");
    await this.autoRefresh();
  }

  async ionViewDidLoad() {
    console.log("ionViewDidLoad AnnouncePage");
  }

  async AddAnnounce() {
    const confirm = await this.alertCtrl.create({
      // title: '<img src="../../assets/imgs/folder.png" />',
      subTitle: "อัพโหลดประกาศ",
      message: "อัพโหลดประกาศใช่หรือไม่?",
      cssClass: "alertCustomCssdetail",
      enableBackdropDismiss: false,
      buttons: [
        {
          text: "ตกลง",
          handler: () => {
            let data = this.Forms.value;
            // console.log(data.anndata);

            this.db
              .getServiceJson({
                type: "announce",
                data: data.anndata,
              })
              .then((data: any) => {
                // console.log(data);
                if (data.status == 200) {
                  this.notifications("อัพโหลดสำเร็จ");
                  this.autoRefresh();
                } else {
                  this.notifications("อัพโหลดไม่สำเร็จ");
                }
              });
          },
        },
        {
          text: "ยกเลิก",
          handler: async () => {},
        },
      ],
    });
    await confirm.present();
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: "bottom",
    });
    await toast.present();
  }
}



