import { Component } from "@angular/core";
import { NavController, NavParams, ToastController } from "ionic-angular";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { LoginPage } from "../login/login";
import { ServiceProvider } from "../../providers/service/service";


/**
 * Generated class for the RegisterPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-register",
  templateUrl: "register.html",
})
export class RegisterPage {
  public registerForm: FormGroup;
  public registerError: string;

  constructor(
    public formBuilder: FormBuilder,
    public navCtrl: NavController,
    public navParams: NavParams,
    public auth: ServiceProvider,
    public toastCtrl: ToastController
  ) {
    this.registerForm = formBuilder.group({
      role: ["", Validators.compose([Validators.required])],
      email: ["", Validators.compose([Validators.required, Validators.email])],
      password: [
        "",
        [
          Validators.required,
          Validators.minLength(6),
          Validators.maxLength(50),
        ],
      ],
      name: ["", Validators.compose([Validators.required])],
    });
  }

  async signup() {
    let data = this.registerForm.value;
    let credentials = {};

    if (!data.email && !data.role && !data.name && data.password) {
      return;
    }

    if (data.role === "ADMIN" && !data.email.endsWith("@maruyasu.co.th")) {
      this.notifications("อีเมลไม่ถูกต้อง");
      return;
    }

    if (data.role === "USER" && data.email.endsWith("@maruyasu.co.th")) {
      this.notifications("อีเมลไม่ถูกต้อง");
      return;
    }

    if (data.role) {

      switch (data.role) {
        case "USER": {
          credentials = {
            type: "adduser",
            role: 'USER',
            email: data.email,
            password: data.password,
            name: data.name,
          };

          break;
        }
        case "ADMIN": {
          credentials = {
            type: "adduser",
            role: 'ADMIN',
            email: data.email,
            password: data.password,
            name: data.name,
          };
          break;
        }
      }

      // console.log(credentials);

      await this.auth.getServiceJson(credentials).then(async (data: any) => {
        // console.log(data.status);
        if (data != null) {
          if (data.status === 200) {
            if (data.result) {
              this.notifications("ยินดีต้อนรับเข้าสู่ระบบ");
              await this.navCtrl.setRoot(LoginPage);
            }
          } else if (data.status === 201) {
            this.notifications("อีเมลถูกใช้งานแล้ว");
          }
        } else {
          this.notifications("อีเมลถูกใช้งานแล้ว");
        }
      }).catch((error) => {
        this.registerError = error.message;
      });
    }
  }

  async btnlogin() {
    await this.navCtrl.setRoot(LoginPage);
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: "top",
    });
    await toast.present();
  }
}



