import { Component } from "@angular/core";
import {
  AlertController,
  App,
  LoadingController,
  NavController,
  NavParams,
  ToastController,
} from "ionic-angular";
import { ServiceProvider } from "../../providers/service/service";
import { StatusPage } from "../status/status";

/**
 * Generated class for the DocslistPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-docslist",
  templateUrl: "docslist.html",
})
export class DocslistPage {
  public uid: any;
  public role: any;
  public loading: any;
  public email: any;
  public password: any;
  public name: any;
  public contact: any;
  public doclists: any;

  constructor(
    public app: App,
    public navCtrl: NavController,
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public toastCtrl: ToastController,
    public db: ServiceProvider
  ) {
    this.uid = localStorage.getItem("uidmitwork");
    this.role = localStorage.getItem("role");

    this.db.getServiceJson({ "type": "account", "user_id": this.uid }).then(async (data: any) => {
      // console.log(data);
      if (data != null) {
        if (data.status == 200) {
          this.email = await data.result.email;
          this.password = await data.result.password;
          this.name = await data.result.name;
        }
      }
    });

    this.db.getServiceJson({ "type": "doclists", "user_id": this.uid }).then(async (data: any) => {
      // console.log(data);
      if (data != null) {
        if (data.status == 200) {
          this.doclists = await data.result;
        }
      }
    });
  }

  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        this.uid = localStorage.getItem("uidmitwork");
        await this.db.getServiceJson({ "type": "account", "user_id": this.uid }).then(async (data: any) => {
          // console.log(data);
          if (data != null) {
            if (data.status == 200) {
              this.email = await data.result.email;
              this.password = await data.result.password;
              this.name = await data.result.name;
              await this.loading.dismiss();
            }
          }

          this.db.getServiceJson({ "type": "doclists", "user_id": this.uid }).then(async (data: any) => {
            // console.log(data);
            if (data != null) {
              if (data.status == 200) {
                this.doclists = await data.result;
              }
            }
          });

        });
      });
      refresher.complete();
    }, 1000);
  }

  async autoRefresh() {
    const refresher = {
      complete: () => {
        // console.log("Refresher complete");
      },
    };
    await this.doRefresh(refresher);
  }

  async ionViewWillEnter() {
    // console.log("ionViewWillEnter");
    await this.autoRefresh();
  }

  async ionViewDidLoad() {
    // await console.log("ionViewDidLoad DocslistPage");
  }

  async status_docs(data) {
    this.navCtrl.push(StatusPage, { data: data });
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: "bottom",
    });
    await toast.present();
  }
}



