  async deletework(file, work_id) {
    console.log(file, work_id);
    if (file == null && work_id == null) {
      this.notifications("ไม่สามารถลบได้");
      return;
    }
    const confirm = await this.alertCtrl.create({
      message: "ต้องการลบ" + this.title + "หรือไม่?",
      cssClass: "alertCustomCssdelete",
      enableBackdropDismiss: false,
      buttons: [
        {
          text: "ลบ",
          handler: () => {
            this.db.getServiceJson({
              "type": "deletework",
              "work_id": work_id,
              "codegen": file.codegen
            }).then((data: any) => {
              // console.log(data);
              if (data.status == 200) {
                this.notifications("เพิ่มเอกสารสำเร็จ");

                const confirm = this.alertCtrl.create({
                  subTitle: this.title,
                  message:
                    "เลข Transaction ID :" + data.result +
                    "<br> * สามารถตรวจสอบสถานะข้อมูล" +
                    "<br> ได้จากเลข ID Number",
                  cssClass: "alertCustomCssdetail",
                  enableBackdropDismiss: false,
                  buttons: [
                    {
                      text: "ตกลง",
                      handler: () => {
                        this.menu = 'work'
                        this.autoRefresh();
                        this.doclistwork = null;
                      },
                    },
                  ],
                });
                confirm.present();
              } else {
                this.notifications("เพิ่มเอกสารไม่สำเร็จ");
              }
            });
          }
        },
        {
          text: "ยกเลิก",
          handler: async () => { },
        },
      ],
    });
    await confirm.present();
  }


  async deletetraining(file) {
    // console.log(file);
    const confirm = await this.alertCtrl.create({
      message: "ต้องการลบ" + this.title + "หรือไม่?",
      cssClass: "alertCustomCssdelete",
      enableBackdropDismiss: false,
      buttons: [
        {
          text: "ลบ",
          handler: () => {
            this.db.getServiceJson({
              "type": "deletetraining",
              "training_id": file.training_id,
              "codegen": file.codegen
            }).then((data: any) => {
              // console.log(data);
              if (data.status == 200) {

                if (file.file_id == null) {
                  this.notifications("ยกเลิกการเข้าอบรมเรียบร้อย");
                  this.traininglist = null;
                  this.menu = 'list';
                } else {

                  this.db.getServiceJson({
                    "type": "deletefiletraining",
                    "file_id": file.file_id
                  }).then((data: any) => {
                    // console.log(data);
                    if (data.status == 200) {

                      if (file == null) {
                        this.notifications("ยกเลิกการเข้าอบรมเรียบร้อย");
                        this.traininglist = null;
                        this.menu = 'list';
                      } else {

                        this.notifications("ยกเลิกการเข้าอบรมเรียบร้อย");
                        this.traininglist = null;
                        this.menu = 'list';
                      }

                    } else {
                      this.notifications("ยกเลิกการเข้าอบรมไม่สำเร็จ");
                    }
                  });


                }

              } else {
                this.notifications("ยกเลิกการเข้าอบรมไม่สำเร็จ");
              }
            });
          }
        },
        {
          text: "ยกเลิก",
          handler: async () => { },
        },
      ],
    });
    await confirm.present();

  }


  async lookfile(file) {
    const url = environment.URL_Work_download + file;
    await window.open(url, "_blank");
  }


  async onSegmentSelect(selectedMenu) {
    this.menu = await selectedMenu.value;
    this.autoRefresh();
  }


  async generateRandomCode() {
    const letters = Array.from("ABCDEFGHIJKLMNOPQRSTUVWXYZ");
    const numbers = Array.from("0123456789");

    // Generate 4 random letters
    const randomLetters = Array.from(
      { length: 4 },
      () => letters[Math.floor(Math.random() * letters.length)]
    ).join("");

    // Generate 4 random numbers
    const randomNumbers = Array.from(
      { length: 4 },
      () => numbers[Math.floor(Math.random() * numbers.length)]
    ).join("");

    this.codegen = randomLetters + randomNumbers;
  }

  // menu 1
  async folders(rootfolder) {
    this.navCtrl.push(FolderdetailPage, { data: rootfolder });
  }

  // menu 2
  async AddWork() {
    const confirm = await this.alertCtrl.create({
      message: "ต้องการยืนยัน" + this.title + "หรือไม่?",
      cssClass: "alertCustomCssdetail",
      enableBackdropDismiss: false,
      buttons: [
        {
          text: "ตกลง",
          handler: () => {
            let data = this.FormsWork.value;
            if (!data) {
              return;
            }
            this.generateRandomCode();
            if (this.codegen) {
              this.db.getServiceJson({
                "type": "newWork",
                "user_id": this.uid,
                "work_type_id": this.worktype,
                "owner_name": data.names,
                "owner_company": data.company,
                "work_detail": data.workDetails,
                "startdate": data.startDate,
                "enddate": data.endDate,
                "codegen": this.codegen,
                "work_name": this.title,
                "date": data.startDate,
                "status_work1": "Pending",
                "status_work2": "Pending",
                "status_work3": "Pending",
                "status_work4": "Pending",
                "status_work5": "Pending",
                "remarks": null
              }).then((data: any) => {
                // console.log(data);
                if (data.status == 200) {
                  this.notifications("เพิ่มเอกสารสำเร็จ");
                  const confirm = this.alertCtrl.create({
                    subTitle: this.title,
                    message:
                      "เลข Transaction ID :" +
                      data.result +
                      "<br> สามารถตรวจสอบสถานะข้อมูลได้จากเลข ID Number",
                    cssClass: "alertCustomCssdetail",
                    enableBackdropDismiss: false,
                    buttons: [
                      {
                        text: "ตกลง",
                        handler: () => {
                          this.menu = 'work'
                          // this.autoRefresh();
                        },
                      },
                    ],
                  });
                  confirm.present();
                } else {
                  this.notifications("เพิ่มเอกสารไม่สำเร็จ");
                }
              });

            } else {
              this.notifications("โค้ดเจนไม่ทำงาน");
            }

          },
        },
        {
          text: "ยกเลิก",
          handler: async () => { },
        },
      ],
    });
    await confirm.present();
  }


