import { Component } from "@angular/core";
import {
  AlertController,
  App,
  LoadingController,
  NavController,
  NavParams,
  ToastController,
} from "ionic-angular";
import { ServiceProvider } from "../../providers/service/service";
import { DocsuserPage } from "../docsuser/docsuser";

/**
 * Generated class for the UserPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-user",
  templateUrl: "user.html",
})
export class UserPage {
  public uid: any;
  public role: any;
  public loading: any;
  public email: any;
  public password: any;
  public name: any;
  public contact: any;

  constructor(
    public app: App,
    public navCtrl: NavController,
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public toastCtrl: ToastController,
    public db: ServiceProvider
  ) {
    this.uid = localStorage.getItem("uidmitwork");
    this.role = localStorage.getItem("role");

    this.db.getServiceJson({ type: "account", user_id: this.uid }).then(async (data: any) => {
      // console.log(data);
      if (data != null) {
        if (data.status == 200) {
          this.email = await data.result.email;
          this.password = await data.result.password;
          this.name = await data.result.name;
        }
      }
    });
  }

  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        this.uid = localStorage.getItem("uidmitwork");
        await this.db.getServiceJson({ type: "account", user_id: this.uid }).then(async (data: any) => {
          // console.log(data);
          if (data != null) {
            if (data.status == 200) {
              this.email = await data.result.email;
              this.password = await data.result.password;
              this.name = await data.result.name;
              await this.loading.dismiss();
            }
          }
        });
      });
      refresher.complete();
    }, 1000);
  }

  async autoRefresh() {
    const refresher = {
      complete: () => {
        console.log("Refresher complete");
      },
    };
    await this.doRefresh(refresher);
  }

  async ionViewWillEnter() {
    console.log("ionViewWillEnter");
    await this.autoRefresh();
  }

  async ionViewDidLoad() {
    console.log("ionViewDidLoad UserPage");
  }

  async menu(number: number) {
    let title;
    switch (number) {
      case 1:
        title = "รายชื่อบริษัท";
        break;
      case 2:
        title = "การอบรม";
        break;
      case 3:
        title = "งานที่สูง";
        break;
      case 4:
        title = "งานความร้อน";
        break;
      case 5:
        title = "งานอับอากาศ";
        break;
      case 6:
        title = "งานสารเคมี";
        break;
      case 7:
        title = "ติดต่อSafety";
        break;
    }

    if (title) {
      await this.navCtrl.push(DocsuserPage, { data: { title } });
    }
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: "bottom",
    });
    await toast.present();
  }
}



