import { Component } from "@angular/core";
import {
  AlertController,
  App,
  LoadingController,
  NavController,
  NavParams,
} from "ionic-angular";
import { ToastController } from "ionic-angular";
import { ServiceProvider } from "../../providers/service/service";
import { DocsPage } from "../docs/docs";

@Component({
  selector: "page-home",
  templateUrl: "home.html",
})
export class HomePage {
  public uid: any;
  public role: any;
  public loading: any;
  public username: any;
  public password: any;
  public name: any;

  constructor(
    public app: App,
    public navCtrl: NavController,
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public toastCtrl: ToastController,
    public db: ServiceProvider
  ) {
    this.uid = localStorage.getItem("uidmitwork");
    this.role = localStorage.getItem("role");

    this.db
      .getServiceJson({ type: "account", user_id: this.uid })
      .then(async (data: any) => {
        // console.log(data);
        if (data != null) {
          if (data.status == 200) {
            this.username = await data.result.username;
            this.password = await data.result.password;
            this.name = await data.result.name;
          }
        }
      });
  }

  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        this.uid = localStorage.getItem("uidmitwork");
        await this.db
          .getServiceJson({ type: "account", user_id: this.uid })
          .then(async (data: any) => {
            // console.log(data);
            if (data != null) {
              if (data.status == 200) {
                this.username = await data.result.username;
                this.password = await data.result.password;
                this.name = await data.result.name;
                await this.loading.dismiss();
              }
            }
          });
      });
      refresher.complete();
    }, 1000);
  }

  async autoRefresh() {
    const refresher = {
      complete: () => {
        console.log("Refresher complete");
      },
    };
    await this.doRefresh(refresher);
  }

  async ionViewWillEnter() {
    console.log("ionViewWillEnter");
    await this.autoRefresh();
  }

  async ionViewDidLoad() {
    console.log("ionViewDidLoad AccountPage");
  }

  async menu(number: number) {
    let pack = {};
    switch (number) {
      case 1:
        pack = {
          title: "เพิ่มเอกสาร",
          content: "",
        };
        await this.navCtrl.push(DocsPage, { data: pack });
        break;
      case 2:
        pack = {
          title: "การอบรม",
          content: "",
        };
        await this.navCtrl.push(DocsPage, { data: pack });
        break;
      case 3:
        pack = {
          title: "งานที่สูง",
          content: "",
        };
        await this.navCtrl.push(DocsPage, { data: pack });
        break;
      case 4:
        pack = {
          title: "งานสารเคมี",
          content: "",
        };
        await this.navCtrl.push(DocsPage, { data: pack });
        break;
      case 5:
        pack = {
          title: "งานความร้อน",
          content: "",
        };
        await this.navCtrl.push(DocsPage, { data: pack });
        break;
      case 6:
        pack = {
          title: "งานอับอากาศ",
          content: "",
        };
        await this.navCtrl.push(DocsPage, { data: pack });
        break;
      case 7:
        pack = {
          title: "รายชื่อบริษัท",
          content: "",
        };
        await this.navCtrl.push(DocsPage, { data: pack });
        break;
      case 8:
        pack = {
          title: "เพิ่มการติดต่อ",
          content: "",
        };
        await this.navCtrl.push(DocsPage, { data: pack });
        break;
      default:
        break;
    }
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: "bottom",
    });
    await toast.present();
  }
}



