import { Component } from "@angular/core";
import {
  AlertController,
  App,
  LoadingController,
  NavController,
  NavParams,
  ToastController,
} from "ionic-angular";
import { ServiceProvider } from "../../providers/service/service";
import { LoginPage } from "../login/login";

/**
 * Generated class for the AccountPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-account",
  templateUrl: "account.html",
})
export class AccountPage {
  public uid: any;
  public role: any;
  public loading: any;  
  public email: any;
  public username: any;
  public password: any;
  public name: any;

  constructor(
    public app: App,
    public navCtrl: NavController,
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public toastCtrl: ToastController,
    public db: ServiceProvider
  ) {
    this.uid = localStorage.getItem("uidmitwork");
    this.role = localStorage.getItem("role");

    this.db
      .getServiceJson({ type: "account", user_id: this.uid })
      .then(async (data: any) => {
        // console.log(data);
        if (data != null) {
          if (data.status == 200) {
            this.email = await data.result.email;
            this.username = await data.result.username;
            this.password = await data.result.password;
            this.name = await data.result.name;
          }
        }
      });
  }

  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        this.uid = localStorage.getItem("uidmitwork");
        await this.db
          .getServiceJson({ type: "account", user_id: this.uid })
          .then(async (data: any) => {
            // console.log(data);
            if (data != null) {
              if (data.status == 200) {
                this.email = await data.result.email;
                this.username = await data.result.username;
                this.password = await data.result.password;
                this.name = await data.result.name;
                await this.loading.dismiss();
              }
            }
          });
      });
      refresher.complete();
    }, 1000);
  }

  async autoRefresh() {
    const refresher = {
      complete: () => {
        console.log("Refresher complete");
      },
    };
    await this.doRefresh(refresher);
  }

  async ionViewWillEnter() {
    console.log("ionViewWillEnter");
    await this.autoRefresh();
  }

  async ionViewDidLoad() {
    console.log("ionViewDidLoad AccountPage");
    await this.db
      .getServiceJson({ type: "account", user_id: this.uid })
      .then(async (data: any) => {
        // console.log(data);
        if (data != null) {
          if (data.status == 200) {
            this.email = await data.result.email;
            this.password = await data.result.password;
            this.name = await data.result.name;
          }
        }
      });
  }

  async updateprofile() {
    const confirm = await this.alertCtrl.create({
      // title: '<img src="../../assets/background/logo.png" />',
      subTitle: "อัพเดจข้อมูล",
      message: "ต้องการอัพเดจข้อมูลใช่หรือไม่?",
      cssClass: "alertCustomCssdetail",
      enableBackdropDismiss: false,
      buttons: [
        {
          text: "ตกลง ออกจากระบบ",
          handler: () => {
            this.db
              .getServiceJson({
                type: "updateprofile",
                name: this.name,
                password: this.password,
                user_id: this.uid,
              })
              .then((data: any) => {
                if (data.status == 200) {
                  this.notifications("อัพเดจสำเร็จ ต้องออกจากระบบแล้วเข้าใหม่");
                  this.logout();
                } else {
                  this.notifications("ไม่มีข้อมูลผู้ใช้งาน");
                }
              });
          },
        },
        {
          text: "ยกเลิก",
          handler: async () => {},
        },
      ],
    });
    await confirm.present();
  }

  async logout() {
    const confirm = await this.alertCtrl.create({
      // title: '<img src="../../assets/background/logo.png" />',
      subTitle: "ออกจากระบบ",
      message: "ต้องการออกจากระบบใช่หรือไม่?",
      cssClass: "alertCustomCssdetail",
      enableBackdropDismiss: false,
      buttons: [
        {
          text: "ตกลง ออกจากระบบ",
          handler: () => {
            localStorage.clear();
            this.app.getRootNavs()[0].setRoot(LoginPage);
          },
        },
        {
          text: "ยกเลิก",
          handler: async () => {},
        },
      ],
    });
    await confirm.present();
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: "bottom",
    });
    await toast.present();
  }
}



