import { Component } from "@angular/core";
import { NavController } from "ionic-angular";
import { HomePage } from "../home/home";
import { AccountPage } from "../account/account";
import { AnnouncePage } from "../announce/announce";
import { UserPage } from "../user/user";
import { DocslistPage } from "../docslist/docslist";
import { LoginPage } from "../login/login";

/**
 * Generated class for the TabsPage tabs.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-tabs",
  templateUrl: "tabs.html",
})
export class TabsPage {
  public uid: any;
  public role: any;
  public icon: any;
  public titletabs: any;

  RootHome: any;
  Root: any;
  RootAccount: any;

  constructor(public navCtrl: NavController) {
    this.uid = localStorage.getItem("uidmitwork");
    this.role = localStorage.getItem("role");


    this.icon = this.role === 'USER' ? 'md-document' : 'md-chatbubbles';
    this.titletabs = this.role === 'USER' ? 'ติดตามเอกสาร' : 'ประกาศ';


    if (this.role === "USER") {
      this.RootHome = UserPage;
      this.Root = DocslistPage;
      this.RootAccount = AccountPage;
    } else if (this.role === "ADMIN") {
      this.RootHome = HomePage;
      this.Root = AnnouncePage;
      this.RootAccount = AccountPage;
    }else{
      this.navCtrl.setRoot(LoginPage);
    }
  }
}



